* {
  margin: 0;
  padding: 0;
}

.app {
  text-align: center;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  display: flex;
}

game-board {
  width: 100%;
  display: inline-block;
}

table {
  border-collapse: collapse;
  table-layout: fixed;
  margin: auto;
}

td {
  width: 50px;
  height: 50px;
  border: 2px solid #cecece;
  position: relative;
}

td .dot {
  width: 6px;
  height: 6px;
  background-color: #000;
  border-radius: 50%;
  display: inline-block;
  position: absolute;
}

td .dot-top-left {
  top: -3px;
  left: -3px;
}

td .dot-top-right {
  top: -3px;
  right: -3px;
}

td .dot-bottom-left {
  bottom: -3px;
  left: -3px;
}

td .dot-bottom-right {
  bottom: -3px;
  right: -3px;
}

td .hitbox {
  display: inline-block;
  position: absolute;
}

td .hitbox-top {
  width: 90%;
  height: 10%;
  top: -1%;
  left: 50%;
  right: 50%;
  transform: translate(-50%);
}

td .hitbox-bottom {
  width: 90%;
  height: 10%;
  bottom: -1%;
  left: 50%;
  right: 50%;
  transform: translate(-50%);
}

td .hitbox-right {
  width: 10%;
  height: 90%;
  top: 50%;
  bottom: 50%;
  right: -1%;
  transform: translate(0, -50%);
}

td .hitbox-left {
  width: 10%;
  height: 90%;
  top: 50%;
  bottom: 50%;
  left: -1%;
  transform: translate(0, -50%);
}

td.clicked-left {
  border-left: 2px solid;
}

td.clicked-left.blue-left {
  border-left-color: #00f;
}

td.clicked-left.red-left {
  border-left-color: red;
}

td.clicked-right {
  border-right: 2px solid;
}

td.clicked-right.blue-right {
  border-right-color: #00f;
}

td.clicked-right.red-right {
  border-right-color: red;
}

td.clicked-bottom {
  border-bottom: 2px solid;
}

td.clicked-bottom.blue-bottom {
  border-bottom-color: #00f;
}

td.clicked-bottom.red-bottom {
  border-bottom-color: red;
}

td.clicked-top {
  border-top: 2px solid;
}

td.clicked-top.blue-top {
  border-top-color: #00f;
}

td.clicked-top.red-top {
  border-top-color: red;
}

td.selected-left {
  border-left: 2px solid #000;
}

td.selected-right {
  border-right: 2px solid #000;
}

td.selected-bottom {
  border-bottom: 2px solid #000;
}

td.selected-top {
  border-top: 2px solid #000;
}

td.background-blue {
  background-color: #9692ff;
}

td.background-red {
  background-color: #ff9792;
}

.overlay {
  width: 100%;
  height: 100vh;
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  background-color: #00000080;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.form-container {
  width: 50%;
  height: 20%;
  opacity: 1;
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  place-items: center;
  display: flex;
  position: absolute;
  box-shadow: 0 14px 28px #00000040, 0 10px 10px #00000038;
}

.inputs-container {
  width: 70%;
  flex-direction: row;
  justify-content: space-around;
  margin-bottom: 10px;
  display: flex;
}

.player1-input, .player2-input {
  width: 45%;
}

.size-input {
  width: 10%;
  margin-bottom: 10px;
}

.validate-button {
  width: 30%;
}

.game-infos-container {
  width: 50%;
  text-align: center;
  flex-direction: column;
  align-content: center;
  place-items: center;
  margin: 50px auto auto;
  display: flex;
}

.score-1, .score-2 {
  margin-left: 20px;
  margin-right: 20px;
}

.scores {
  margin-bottom: 10px;
}

